import { s as supabase } from "./supabaseClient.js";
async function getProductsByName(name) {
  console.log("getProductsByName", name);
  const formattedName = name.replace(/\s+/g, " & ");
  const { data, error } = await supabase.from("products").select("*").textSearch("fts", formattedName, { config: "german" }).limit(30);
  if (error) {
    console.log("Fehler beim Abrufen getProductsByName", error);
  } else {
    return data;
  }
}
