async function extractFormData(formData) {
  const url = String(formData.get("url"));
  const category = Number(formData.get("category"));
  return { url, category };
}
async function getNewId(table) {
  console.log("neue id ermitteln");
  const { data: maxid, error } = await supabase.from(table).select("id").order("id", { ascending: false }).limit(1).single();
  if (error) {
    console.log("es gab einen fehler bei der ermittlung der id ", error);
    return fail(400);
  }
  const id = Number(maxid.id) + 1;
  console.log("neue id ist ", table, id);
  return id;
}
async function insertCategory(category, productData) {
  const id = await getNewId("products_categories");
  console.log("neue id: ", id);
  const newcatentry = {
    id,
    product_id: productData.id,
    category_id: category
  };
  console.log("newcatentry ", newcatentry);
  const { error } = await supabase.from("products_categories").insert(newcatentry);
  if (error)
    console.log("error in insertCategory ", error);
  return true;
}
async function insertProductData(productData) {
  const { error } = await supabase.from("products").insert(productData);
  if (error)
    console.log("there was an error adding the new product", error);
  return true;
}
async function getProductData(url, shop) {
  let productData;
  console.log("getproductData()", "\n", url, "\n", shop);
  switch (shop) {
    case "kopp-verlag":
      productData = await getProductDataFromShop(url);
      break;
  }
  return productData;
}
async function getCategories() {
  const { data, error } = await supabase.from("categories").select("id, name").order("name");
  if (error)
    console.log("there was an error in getCategories: ", error);
  else
    return data;
}
async function load() {
  const categories = await getCategories();
  return { categories };
}
